import React,{useState,useEffect} from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import {Button,Slider,Typography} from '@material-ui/core'
import CloudUploadIcon from '@material-ui/icons/CloudUpload';
import htmlToImage from 'html-to-image';
import download from 'downloadjs'
function App() {
    const [linkMainImage,setLinkMainImage] = useState("");
    const [linkTradeImage,setLinkTradeImage] = useState("");
    const [imageScale,setImageScale] = useState(1);
    const [imageRotation,setImageRotation] = useState(0);
    useEffect(() => {
        dragElement(document.getElementById("drag-image"));

        function dragElement(elmnt) {
            var pos1 = 0, pos2 = 0, pos3 = 0, pos4 = 0;
            // otherwise, move the DIV from anywhere inside the DIV:
            elmnt.onmousedown = dragMouseDown;

            function dragMouseDown(e) {
                e = e || window.event;
                e.preventDefault();
                // get the mouse cursor position at startup:
                pos3 = e.clientX;
                pos4 = e.clientY;
                document.onmouseup = closeDragElement;
                // call a function whenever the cursor moves:
                document.onmousemove = elementDrag;
            }

            function elementDrag(e) {
                e = e || window.event;
                e.preventDefault();
                // calculate the new cursor position:
                pos1 = pos3 - e.clientX;
                pos2 = pos4 - e.clientY;
                pos3 = e.clientX;
                pos4 = e.clientY;
                // set the element's new position:
                elmnt.style.top = (elmnt.offsetTop - pos2) + "px";
                elmnt.style.left = (elmnt.offsetLeft - pos1) + "px";
            }

            function closeDragElement() {
                // stop moving when mouse button is released:
                document.onmouseup = null;
                document.onmousemove = null;
                return false
            }
        }
    });

    const handleChangeMainImage = (event) => {
        setLinkMainImage(URL.createObjectURL(event.target.files[0]))
    };
    const handleChangeTradeImage = (event) => {
        setLinkTradeImage(URL.createObjectURL(event.target.files[0]))
    };
    const handleGenerateImage = () => {
        let node = document.getElementById("image-after-render");
        htmlToImage.toPng(node)
            .then(function (dataUrl) {
                let img = new Image();
                img.src = dataUrl;
                document.getElementById("result").appendChild(img);
                download(dataUrl, 'my-node.png');
            })
            .catch(function (error) {
                console.error('oops, something went wrong!', error);
            });
    };
    const handleChangeScale = (event, newValue) => {
        setImageScale(newValue/100)
    };
    const handleChangeRation = (event, newValue) => {
        setImageRotation(newValue)
    };
    return (
        <div className="App container">
            <div className={'row'}>
                <div className={'col-md-4'}>
                    <form noValidate autoComplete="off">
                        <input
                            style={{display: 'none'}}
                            accept="image/*"
                            id="contained-button-file"
                            type="file"
                            onChange={handleChangeMainImage}
                        />
                        <label
                            className={'col-12 mt-4'}
                            htmlFor="contained-button-file">
                            <Button
                                variant="contained"
                                color="default"
                                startIcon={<CloudUploadIcon />}
                                component="span"
                            >
                                Ảnh chính
                            </Button>
                        </label>
                        <input
                            style={{display: 'none'}}
                            accept="image/*"
                            id="contained-button-trademark"
                            type="file"
                            onChange={handleChangeTradeImage}
                        />
                        <label
                            className={'col-12 mt-4'}
                            htmlFor="contained-button-trademark">
                            <Button
                                variant="contained"
                                color="default"
                                startIcon={<CloudUploadIcon />}
                                component="span"
                            >
                                Ảnh trade mark
                            </Button>
                        </label>
                        <Typography
                            className={'col-12 mt-4'}
                            id="continuous-slider"
                            gutterBottom>
                            Kích thước ảnh Trade mark
                        </Typography>
                        <Slider value={imageScale*100} color={'secondary'} onChange={handleChangeScale} aria-labelledby="continuous-slider" />
                        <Typography
                            className={'col-12 mt-4'}
                            id="continuous-slider"
                            gutterBottom>
                            Xoay ảnh Trade mark
                        </Typography>
                        <Slider max={360} min={0} value={imageRotation} color={'secondary'} onChange={handleChangeRation} aria-labelledby="continuous-slider" />
                        <Button className={' mt-4 '} onClick={handleGenerateImage} variant="outlined" color="secondary">
                            Sinh ảnh
                        </Button>
                    </form>
                </div>
                <div className={'col-md-8 row mt-5 align-items-center justify-content-center'}>
                    <div className={'col-md-6'} id={'image-after-render'} style={{position: 'relative', overflow: 'hidden'}}>
                        {linkMainImage && <img alt={'anh-chinh'} className={'img-fluid img-main'} src={linkMainImage}/>}
                        <div id={'drag-image'} style={{ width: '100%', height: '100%' , position: 'absolute' , top: 0 }}>
                            {
                                linkTradeImage ?
                                <img
                                    style={{
                                        transform: "scale("+imageScale+") rotate(" + imageRotation + "deg)"
                                    }}
                                    className={'img-fluid img-trademark'}
                                    alt='Test Image'
                                    src={linkTradeImage} /> :
                                <div/>
                            }
                        </div>
                    </div>
                    <div className={'col-md-6'} id={'result'}>
                    </div>
                </div>
            </div>
        </div>
    );
}

export default App;
